/* price section start  */
.boxes__section {
  display: flex;
  margin: 0.5em 0 0.7em 0em;
}
.price__section {
  height: 50px;
  width: 70px;
  background-color: rgb(243, 225, 203);
  border-radius: 10px;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 0.1em;
  margin: 0.7em 0 0 0;
}

.price__number {
  font-size: 1.7em;
  color: rgb(190, 77, 12);
  margin-bottom: 4px;
}

.price__symbol {
  font-size: 0.8em;
  color: rgb(208, 128, 30);
  margin: 8px 0 0 1px;
}

/* single buy start */
.single__buy__section {
  height: 48px;
  width: 68px;
  background-color: rgb(255, 255, 255);
  border-radius: 10px;
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 0.7em 0 0 0.5em;

  border: 2px solid;
  border-color: rgb(190, 77, 12);
}

.single__buy__text {
  color: rgb(190, 77, 12);
  font-size: 1.5em;
  margin-bottom: 2.5px;
}

.bulk__buy__section {
  height: 48px;
  width: 68px;
  background-color: rgb(255, 255, 255);
  border-radius: 10px;
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 0.7em 0 0 0.5em;

  border: 2px solid;
  border-color: rgb(210, 210, 210);
}

.bulk__buy__text {
  color: rgb(190, 190, 190);
  font-size: 1.5em;
  margin-bottom: 2.5px;
}

.discount__section {
  height: 50px;
  width: 100px;
  background-color: rgb(243, 225, 203);
  border-radius: 10px;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 0.1em;
  margin: 0.7em 0 0 0.7em;
  /* border: 2px solid indianred; */
}

.discount__number {
  font-size: 1.7em;
  color: rgb(190, 77, 12);
  margin: 0 2px 4px 0;
  text-decoration: line-through;
}
