/* meta start */
.meta__section {
  margin: 3px 0 0 0;
  display: flex;
  align-items: center;
}

.meta__merchant {
  color: rgb(217, 169, 169);
}

.meta__merchant__name {
  font-weight: 700;
  color: brown;
}

.meta__rating__star__section {
  margin: 0 0 0 0.5em;
}

.meta__rating__value,
.meta__rating__count {
  font-size: 0.75em;
  margin: 0 0 0 0.5em;
  color: rgb(143, 159, 159);
  padding-bottom: 3.5px;
}
