.faq__section {
  margin: 3em 1em 1em 1em;
  /* border: 1px solid gainsboro; */
  /* height: 200vh; */
  border-radius: 10px;
  box-shadow: rgba(100, 100, 111, 0.2) 0px 7px 29px 0px;
  padding: 0.5em 2em;
  /* width: 30vh; */
}

.faq__title {
  font-size: 2em;
}

.qna__section {
  text-align: left;
