.info__section {
  height: 320px;
  text-align: left;
  padding: 0 1em 0 0;
  /* width: 35vh; */
  margin: 0 1em;
}

.info__title {
  margin: 1em 0 0 0;
}

.info__desc {
  margin: 0.75em 0 1em 0;
  font-weight: 300;
  font-size: 0.8em;
  /* width: 400px; */
}

.info__buy__button {
  margin: 0.5em 0;
  background: linear-gradient(to left, #f12711, #f5af19);
  font-size: 1.1em;
  color: rgb(255, 255, 255);

  width: 125px;
  height: 37px;

  font-weight: 700;
  border: 0px;
  border-radius: 3px;
}
