.product__section {
  /* background-color: bisque;
   */
  background-color: white;
  display: flex;
  /* padding: 0 1em 0 0; */
  /* margin: 1em 1em 0 0; */
  flex-wrap: wrap;
  justify-content: center;
  /* align-items: center; */
}

.product__image {
  height: 300px;
  width: 100%;
}

.product__image__section {
  position: relative;
  text-align: center;
}

.product__image__overlayText {
  font-size: 1.5em;
  font-weight: bold;
  color: white;
  text-align: right;
  overflow: hidden;

  position: absolute;
  top: 0px;
  left: 0px;

  background-color: rgb(228, 71, 19);
  width: 90px;
  border: 1px solid rgb(0, 0, 0);
  border-radius: 20px;
}
